import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    posterImg: null, // 海报 图片
    qrcodeImg: null // 二维码 图片
  },
  mutations: {
    setPosterImg(state, img) {
      state.posterImg = img
    },
    setQrcodeImg(state, img) {
      state.qrcodeImg = img
    }
  },
  actions: {},
  modules: {}
})
