<template>
  <div class="upload-pane-wrapper">
    <div class="common-row">
      <mt-button type="primary" size="small" @click="handleClickUpload('POSTER')">上传海报</mt-button>
      <mt-button type="primary" size="small" @click="handleClickUpload('QRCODE')">上传二维码</mt-button>
    </div>

    <div class="filename-list">
      <p>海报文件名: {{ this.posterFile && this.posterFile.name }}</p>
      <p>二维码文件名: {{ this.qrcodeFile && this.qrcodeFile.name }}</p>
    </div>

    <div v-show="false">
      <input type="file" ref="poster-input" @change="posterUploadChange" />
      <input type="file" ref="qrcode-input" @change="qrcodeUploadChange" />
    </div>
  </div>
</template>

<script>
import * as utils from '../utils'

export default {
  name: 'UploadPane',
  data() {
    return {
      posterFile: null,
      qrcodeFile: null
    }
  },
  methods: {
    handleClickUpload(type) {
      if (type === 'POSTER') {
        this.$refs['poster-input'].click()
      } else {
        this.$refs['qrcode-input'].click()
      }
    },
    async posterUploadChange(event) {
      this.uploadChange(event, 'POSTER')
    },
    async qrcodeUploadChange(event) {
      this.uploadChange(event, 'QRCODE')
    },
    async uploadChange(event, type) {
      const file = event.target.files[0]
      if (!file) {
        return this.$Toast({ message: '取消上传' })
      }
      const fileBase64 = await utils.readImgFile(file)

      const img = await utils.loadImage(fileBase64)

      if (type === 'POSTER') {
        await this.handlePosterUploadChange(file, img)
      } else {
        await this.handleQrcodeUploadChange(file, img)
      }
    },
    async handlePosterUploadChange(file, img) {
      this.posterFile = file
      this.$store.commit('setPosterImg', img)
    },
    async handleQrcodeUploadChange(file, img) {
      this.qrcodeFile = file
      const canvas = document.createElement('canvas')
      const ctx = canvas.getContext('2d')

      const qrcodeInfo = {
        x: (244 / 861) * img.width,
        y: (535 / 1383) * img.height,
        width: (375 / 861) * img.width,
        height: (375 / 1383) * img.height
      }

      // 设置 canvas 基础样式
      canvas.width = String(Math.round(qrcodeInfo.width))
      canvas.height = canvas.width
      canvas.style.width = '100%'

      ctx.drawImage(
        img,
        -1 * qrcodeInfo.x,
        -1 * qrcodeInfo.y,
        img.width,
        img.height
      )

      const dataUrl = canvas.toDataURL('image/png')
      const qrcodeImg = await utils.loadImage(dataUrl)

      this.$store.commit('setQrcodeImg', qrcodeImg)
    }
  }
}
</script>

<style lang="scss" scoped>
.upload-pane-wrapper {
  .mint-button + .mint-button {
    margin-left: 2vw;
  }
  .filename-list {
    padding: 1vw 4vw;
    text-align: left;
    p {
      margin: 1vw 0;
    }
  }
}
</style>
