<template>
  <div class="preview-wrapper">
    <div class="common-row operator">
      <mt-button
        type="primary"
        size="small"
        :disabled="disabled"
        @click="draw"
      >{{ base64Img ? '重新生成' : '生成' }}</mt-button>
      <mt-button type="primary" size="small" :disabled="disabled" @click="download">下载</mt-button>
    </div>

    <div class="divide" />

    <div v-if="base64Img">
      <img :src="base64Img" />
    </div>

    <!-- 预览区域 -->
    <div v-show="false">
      <canvas ref="canvas" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Preview',
  data() {
    return {
      ctx: null,
      base64Img: null
    }
  },
  computed: {
    ...mapState([
      'posterImg', //
      'qrcodeImg'
    ]),

    disabled() {
      return !this.posterImg || !this.qrcodeImg
    }
  },
  watch: {
    posterImg(val) {
      this.$nextTick(() => {
        this.draw()
      })
    },
    qrcodeImg(val) {
      this.$nextTick(() => {
        this.draw()
      })
    }
  },
  methods: {
    draw() {
      if (this.disabled) {
        return
      }

      this.initCanvas()

      // 绘制海报
      this.ctx.drawImage(this.posterImg, 0, 0)

      // 绘制二维码
      const qrcodeInfo = {
        x: (795 / 1125) * this.posterImg.width,
        y: (1906 / 2436) * this.posterImg.height,
        width: (250 / 1125) * this.posterImg.width,
        height: (250 / 2436) * this.posterImg.height
      }
      this.ctx.drawImage(
        this.qrcodeImg,
        qrcodeInfo.x,
        qrcodeInfo.y,
        qrcodeInfo.width,
        qrcodeInfo.height
      )

      const canvas = this.$refs['canvas']
      this.base64Img = canvas.toDataURL('image/png')
    },
    initCanvas() {
      const canvas = this.$refs['canvas']
      const ctx = canvas.getContext('2d')

      canvas.width = this.posterImg.width
      canvas.height = this.posterImg.height
      canvas.style.width = '100%'

      this.ctx = ctx
    },
    download() {
      const a = document.createElement('a')
      a.download = `pic_${Date.now()}.png`
      a.href = this.base64Img

      // 方法 1.
      a.click()

      // // 方法 2.
      // const event = document.createEvent('MouseEvents')
      // event.initMouseEvent(
      //   'click',
      //   true,
      //   false,
      //   window,
      //   0,
      //   0,
      //   0,
      //   0,
      //   0,
      //   false,
      //   false,
      //   false,
      //   false,
      //   0,
      //   null
      // )
      // a.dispatchEvent(event)
    }
  }
}
</script>

<style lang="scss" scoped>
.preview-wrapper {
  .operator {
    padding-bottom: 4vw;
    .mint-button + .mint-button {
      margin-left: 2vw;
    }
  }
  .divide {
    content: ' ';
    margin: 0 auto;
    width: 90%;
    height: 1px;
    background-color: #ddd;
  }
  img {
    width: 100%;
  }
}
</style>
